import Vue from 'vue';
import Vuex from 'vuex';
// import * as actions from './actions';
// import * as getters from './getters';
// import * as uz from '../common/js/uz';
// 告诉 vue “使用” vuex
Vue.use(Vuex);
const NAME_TITLE = ['welfare', 'day', 'android', 'ios', 'web'];
// const type =["UPDATE_LOADING"];
// 创建一个对象来保存应用启动时的初始状态
const state = {
  'headerTitle': '福利',
  'menus': NAME_TITLE,
  'menuShow': false,
  // 显示加载进度条
  'loadingShow': false,
  'news': 5,
  'testState': '我是测试用的state'
};
const actions = {
  incrementCounter ({ dispatch, state }) {
    console.log('调用action()');
    console.log(state);
  }
};
const getters = {
  getHeaderTitle (state) {
  return state.headerTitle;
  }
};
// 创建一个对象存储一系列我们接下来要写的 mutation 函数
const mutations = {
  // TODO: 放置我们的状态变更函数,headerTitle,loadingShow,menuShow,menus,news，更新state内容
  UPDATE_TITLE(st1, title) {
    console.log('更新state.headerTitle = ' + title);
    st1.headerTitle = title;
  },
  UPDATE_MENU_SHOW(st2) {
    console.log('菜单当前状态' + (!st2.menuShow ? '打开' : '关闭'));
    st2.menuShow = !st2.menuShow;
  },
  UPDATE_LOADING(st3, data) {
    console.log((data ? '正在' : '结束') + '加载数据');
    st3.loadingShow = data;
  },
  UPDATE_NEWS(st4) {
    st4.news = 0;
  }
};

// 整合初始状态和变更函数，我们就得到了我们所需的 store
// 至此，这个 store 就可以连接到我们的应用中
export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
});



